//!##################################//
//!              STATE               //
//!##################################//
const state = {
  currentPage: '',
  previousPage: '',
  view: []
}

//!##################################//
//!             GETTERS              //
//!##################################//
const getters = {
  getCurrentPage(state) {
    return state.currentPage
  },
  getPreviousPage(state) {
    state.previousPage = this.$router.options.history.state.back
    return state.previousPage
  },
  getView(state) {
    return state.view[state.view.length -1]
  },
  getPreviousView(state) {
    if (state.view[state.view.length -2] == undefined) {
      return '/'
    }
    return state.view[state.view.length -2]
  }
}

//!##################################//
//!            MUTATIONS             //
//!##################################//
const mutations = {
  setCurrentPage(state, page) {
    state.currentPage = page
  },
  setPreviousPage(state, page) {
    state.previousPage = page == '/login' ? '/' : page
  },
  addView(state, view) {
    state.view.push(view)
    console.log('view : ', view);
  },
  previousView(state) {
    state.view.splice(-1, 1)
  }
}

//!##################################//
//!             ACTIONS              //
//!##################################//
const actions = {

}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}